.manaControls {
  height: 110px;
  display: flex;
  color: #333;
}

.manaControl {
  flex: 1 1 0;
  position: relative;
}

.manaSymbols {
  max-width: 51px;
  pointer-events: none;
}

.manaSymbol {
  margin: 1px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.2);
}

.manaPlaceholder {
  opacity: 0.1;
  filter: grayscale(1);
  width: 40px;
  height: 40px;
}

.manaCount {
  font-size: 13px;
  margin-left: 8px;
  position: relative;
  top: -4px;
}
